ul{
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
li{
    padding: 10px;
    width: 250px;
    max-width: 250px;
    max-height: 400px;
}
.card{ 
    margin: auto;
    padding: 5px;
     width: 100%;
     height: 25rem;
     box-shadow: 5px 5px 10px #ccc;
     display: flex;
     flex-direction: column;
     justify-content: flex-start;
}
.card > img{
    width:100%;
    height: 50%;
}
.card article{
    padding: 5px;
}
.card h3{
    padding: 4px;
}
.card p{
    padding: 3px;
}
